export default function pickCard() {

    let Prayer = "Love surrounds me. Faith supports me. Wisdom guides me.";

    //a random number between 0 and 77
    let cardIndex = Math.floor((Math.random() * 78));

    //if random number between 0 and 1 = 1
    let isReversed = (Math.floor((Math.random() * 2)) === 1);

    return {
        prayer: Prayer,
        cardIndex: cardIndex,
        isReversed: isReversed,
    };

}
