import React, { Component } from 'react';
import CardView from '../components/CardView';
import pickCard from '../logic/pickCard';

export class OneCard extends Component {
    static displayName = OneCard.name;

    constructor(props) {
        super(props);

        this.state = {
            cardIndex: null,
            cardMeaning: null
        };

    }

    handleClick = () => {

        var theCard = pickCard();
        this.setState(
            {
                cardIndex: theCard.cardIndex,
                cardMeaning: (theCard.isReversed ? 'R' : 'N')
            }
        );

    }

    render() {
        return (
            <div>
                <div class='header'>
                    <div style={{cursor: "pointer"}} onClick={event => window.location.href = '/'}>Touch Tarot</div><br />
                    <button onClick={this.handleClick} className="button">
                        Draw A Card
                    </button>
                </div>
                <div>
                    <CardView cardMeaning={this.state.cardMeaning} cardIndex={this.state.cardIndex} />
                </div>
            </div>
        );
    }
}
