import React, { Component } from 'react';
import '../CardView.css';
import cardData from '../logic/cardData.js'

class CardView extends Component {

    render() {

        let cardIndex = this.props.cardIndex;
        let cardMeaning = this.props.cardMeaning; //N = Normal, R = Reversed, B = Both

        if (cardIndex == null) {
            return (
                <div className="CardText">
                </div>
            );
        }

        let thisCard = cardData.cardData[cardIndex];
        let imgSrc = require('../img/cards/' + thisCard.ImgFile);

        //console.log('isReversed: ' + isReversed);
        //console.log('cardIndex: ' + cardIndex);
        //console.log('imgSrc: ' + imgSrc);
        //console.log(thisCard);

        let divReversed = null;
        let meaningText = null;
        let imgClass = "CardImage";

        switch (cardMeaning) {

            case 'N': //Normal
                meaningText = thisCard.Meaning;
                break;

            case 'R': //Reversed
                meaningText = thisCard.Reversed;
                imgClass += " ReversedImage";
                divReversed = <div className="Reversed">(reversed)<br /></div>
                break;

            case 'B': //Both
                meaningText = thisCard.Meaning + '<br /><br />(reversed)<br />' + thisCard.Reversed;
                break;

            default: //Invalid
                return (
                    <div className="CardText">
                    </div>
                );
        }

        return (
            <div className="CardView">
                <div className="CardName">{thisCard.CardName}</div>
                <div className={imgClass}><img src={imgSrc} alt={thisCard.CardName}></img></div>
                {divReversed}
                <div className="CardMeaning">{meaningText}</div><br />
            </div>
        );

    }

}

export default CardView;
